.wp-block-create-block-myblock {
  border: 1px dotted #f00;
  .container-myblock {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;

    .second-div-myblock {
      display: flex;
      flex-direction: column;
      align-content: flex-start;
      .title1-myblock {
        font-size: 3.2rem;
        font-weight: 700;
      }
      .title2-myblock {
        font-size: 2rem;
        font-weight: 500;
      }
      .btn-myblock {
        border-radius: 20px;
        font-size: 1rem;
      }
    }
  }
}
